import { createContext, useState, useEffect } from "react";

export const ErrorContext = createContext()

const errorObject = {
    'wrong-phone': 'A megadott telefonszm nem megfelel',
    'wrong-date': "A dtum formtuma nem megfelel!",
    'no-appointment': "Erre a napra nem lehet idpontot foglalni!",
    'terms-error': "Nem fogadta el a feltteleket!",
    'select-appointment': "Nem vlasztott idpontot!",
    'no-date': "Nem vlasztott napot!",
    'no-service': "Mg nem vlasztotta ki, hogy milyen szolgltatst venne ignybe!",
    'wrong-credentials': "A megadott nv felttelezheten nem megfelel!",
    'internal-error': "Bels hiba trtnt. Krjk frisstse az oldalt!",
    'already-reserved': "Ez az idpont mr foglalt!",
    'no-content': 'Jelenleg nem lehet lekrdezni az adatokat. Krjk prblja jra ksbb.',
}

export const useError = (setShow) => {

    const [error, setError] = useState()

    useEffect(() => {
        if(!error) return

        if(Object.keys(errorObject).includes(error)){
            setError(errorObject[error])
        }else{
            setError("Megszakadt a kapcsolat a szerverrel.")
        }

        setShow(true)
    }, [error, setShow])

    return [error, setError]

}